import { parsePhoneNumberFromString, AsYouType, CountryCode, NumberType } from 'libphonenumber-js';
import { memoize } from 'tools/memoize';

// TODO: FOODTECHUI-224     

const defaultDefaultCountryCode = 'RU';

//     
// +7 (123123) 12-3=12+3 => +7123123123123
export const filterPhoneInput = (input?: string | null) =>
    (input ?? '')
        .replace(/\+/g, (match, offset) => (offset > 0 ? '' : match)) //   ,  
        .replace(/[^\d+]/g, ''); //  ,    ,  

//       
//
// NB:        
//        8 800
export const formatPhone = memoize(
    (input: string, defaultCountryCode: CountryCode = defaultDefaultCountryCode): string => {
        //   
        if (input === '900') {
            return input;
        }

        const parsedPhone = parsePhoneNumberFromString(input, defaultCountryCode);

        if (!parsedPhone) {
            return '';
        }

        const formattedPhone = parsedPhone.formatInternational();

        //       8 800...
        if (formattedPhone.startsWith('+7 800')) {
            return formattedPhone.replace(/^\+7/, '8');
        }

        return formattedPhone;
    },
);

//   , ,       
const parseNationalNumberInput = (input: string, defaultCountryCode: CountryCode) => {
    const typeFormatter = new AsYouType(defaultCountryCode);

    const formattedInput = typeFormatter.input(filterPhoneInput(input));

    const parsedNumber = typeFormatter.getNumber();

    return { formattedInput, parsedNumber };
};

const formatInternationalNumberInput = (input: string) => new AsYouType().input(input);

//     
export const formatPhoneInput = memoize(
    (input: string, defaultCountryCode: CountryCode = defaultDefaultCountryCode): string => {
        const { formattedInput, parsedNumber } = parseNationalNumberInput(input, defaultCountryCode);

        const parsedInternationalNumberInput = String(parsedNumber?.number ?? '');

        // ,      8 (987) 654-32-1  +78987654321
        //   800899   ,   ,
        //            8  +7
        if (input.startsWith('8') && parsedInternationalNumberInput.startsWith('+78')) {
            return formatPhoneInput(`+7${input.slice(1)}`);
        }

        // ,          
        if (parsedInternationalNumberInput) {
            return formatInternationalNumberInput(parsedInternationalNumberInput);
        }

        return formattedInput;
    },
);

//         
export const normalizePhoneInput = (
    input: string,
    defaultCountryCode: CountryCode = defaultDefaultCountryCode,
): string => {
    return filterPhoneInput(formatPhoneInput(input, defaultCountryCode));
};

//     
//    (, )         undefined
const allowedPhoneTypes: NumberType[] = [undefined, 'FIXED_LINE', 'FIXED_LINE_OR_MOBILE', 'MOBILE'];

const allowedCountries: CountryCode[] = ['RU'];

export const isValidPhone = memoize(
    (input: string, defaultCountryCode: CountryCode = defaultDefaultCountryCode): boolean => {
        const phoneNumber = parsePhoneNumberFromString(input, defaultCountryCode);

        return Boolean(
            phoneNumber?.isValid() &&
                allowedPhoneTypes.includes(phoneNumber.getType()) &&
                phoneNumber.country &&
                allowedCountries.includes(phoneNumber.country),
        );
    },
);
