import { createHashHistory } from 'history';

export const history = createHashHistory();

/**
 *  ,      .
 *    ,    ""    .
 *
 * ,   go(-2)  replace('some-url') ,   
 *  ,      .   
 *  ,       .
 */
export const getWaitNextNavigationEventPromise = () => {
    return new Promise((resolve) => {
        let unListen: () => void;

        const handleLocationChange = () => {
            if (unListen) {
                unListen();
            }

            resolve();
        };

        unListen = history.listen(handleLocationChange);
    });
};
