import { useDebouncedFn as _useDebouncedFn, ThrottleOrDebounceOpts } from 'beautiful-react-hooks';
import { DependencyList } from 'react';

export const debounceTimeoutMs = 200;

interface DebounceOptions extends ThrottleOrDebounceOpts {
    maxWait?: number; //  lodash,     beautiful-react-hooks
}

const defaultOptions: DebounceOptions = {
    leading: true,
    trailing: false,
};

export const useDebouncedFn: typeof _useDebouncedFn = (
    fn,
    wait = debounceTimeoutMs,
    options = defaultOptions,
    dependencies,
) => _useDebouncedFn(fn, wait, options as ThrottleOrDebounceOpts, dependencies);

//  ,   useDebouncedFn,       
//   
//  
export const useDebouncedOnceFn = <F extends Function>(
    fn: F,
    wait = debounceTimeoutMs,
    dependencies?: DependencyList,
) => {
    const options: DebounceOptions = {
        ...defaultOptions,
        maxWait: wait,
    };

    return _useDebouncedFn(fn, wait, options as ThrottleOrDebounceOpts, dependencies);
};
